import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;

  constructor(private validateService : ValidateService,
              private flashMessage : FlashMessagesService,
              private authServie: AuthService,
              private router: Router
              ) { }

  ngOnInit() {
  }
  onRegisterSubmit(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

    // required fields
    if(!this.validateService.validateRegister(user)){
      this.flashMessage.show('please fill in all fields', { cssClass: 'alert-danger', timeout: 3000 })
      return false;
    }
    if(!this.validateService.validateEmail(user.email)){
      // console.log('please enter valide email');
      this.flashMessage.show('Plese enter valid emaile.', { cssClass: 'alert-danger', timeout: 3000 })
      return false;
    }

    // register user
    this.authServie.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessage.show('Successfully registered',{cssClass: 'alert-success', timeout: 3000})
        this.router.navigate(['/login'])
      } else {
        this.flashMessage.show('somethig went wrong', {cssClass: 'alert-danger', timeout: 3000})
        this.router.navigate(['/register'])
      }
    })


  }

}



